.preview-project {
  $margin: 1rem;
  display: inline-block;

  @media (min-width: breakpoint(tablet)) { @include grid(3, $margin); }
  @media (max-width: breakpoint(tablet)) { @include grid(2, $margin); }
  @media (max-width: breakpoint(phone)) { @include grid(1, $margin); }

  margin-right: $margin;
  margin-bottom: $margin;

  background: white;
  filter: grayscale(1);
  @include easing(300ms, filter);
  &:hover { filter: none }

  .preview-project-content {
    width: 100%;
    padding: 1rem;
    border: none;

    .preview-project-cover {
      img {
        width: 100%;
        height: auto;
      }
    }

    .preview-project-infos {
      // see also remote-description component
      .preview-project-info-title {
        font-size: 2rem;
        margin: 1rem 0;
        text-transform: uppercase;
      }

      p {
        color: color(text);
        font-size: 1.4rem;
        line-height: 1.4em;
      }
    }

    // see also remote-description js component
    @media (min-width: breakpoint(desktop)) {
      &.has-remote-description {
        .preview-project-infos { display: none; }
      }
    }
  }
}



